import React from 'react';
import { Link } from 'react-router-dom';
import { withStyles, createMuiTheme } from '@material-ui/core/styles';
import {withTheme } from '@material-ui/styles'
import Button from '@material-ui/core/Button';
import ListIcon from '@material-ui/icons/List';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemText from '@material-ui/core/ListItemText';

const theme = createMuiTheme();

const StyledMenu = withStyles({
  paper: {
    border: '1px solid #d3d4d5',
  },
})(props => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
));


const StyledMenuItem = withStyles(theme => ({
  root: {
    '&:focus': {
      backgroundColor: '#d3d4d5',
      '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
        color: theme.palette.common.white,
      },
    },
  },
}))(MenuItem);

const CustomizedMenus = (props) => {
  const [anchorEl, setAnchorEl] = React.useState(null);

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  // make sure there is no text decoration
  const linkStyles = {
    textDecoration: 'none',
  };

  return (
    <div>
      <Button
        aria-owns={anchorEl ? 'simple-menu' : undefined}
        aria-haspopup="true"
        variant="contained"
        color="primary"
        onClick={handleClick}
      >
        <ListIcon />
      </Button>
      <StyledMenu
        id="simple-menu"
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <StyledMenuItem>
          <Link to="/" style={linkStyles}>
            <ListItemText primary="Home" />
          </Link>
        </StyledMenuItem>
        <StyledMenuItem>
          <a href="https://etherclearapp.github.io/etherclear_docs/#/" style={linkStyles} target={"_blank"}>
            <ListItemText primary="What is Etherclear?" />
          </a>
        </StyledMenuItem>
        <StyledMenuItem>
          <Link to="/" style={linkStyles}>
            <ListItemText primary="Settings" />
          </Link>
        </StyledMenuItem>
      </StyledMenu>
    </div>
  );
}

export default withTheme((CustomizedMenus));
