import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { withStyles, createMuiTheme } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import TextField from "@material-ui/core/TextField";
import SubmitDialog from "./SubmitDialog";
import Grid from "@material-ui/core/Grid";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import MediaQuery from "react-responsive";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import RefreshIcon from "@material-ui/icons/Refresh";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import { withBreakpoints } from 'react-breakpoints';

import CheckCircleIcon from "@material-ui/icons/CheckCircle";

import { CopyToClipboard } from "react-copy-to-clipboard";
import Tooltip from "@material-ui/core/Tooltip";
import Switch from "@material-ui/core/Switch";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import App from "./App";
import Web3 from "web3";

// custom mui theme
const theme = createMuiTheme();

const BigNumber = require("bignumber.js");
const niceware = require("niceware");

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2)
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1)
  },
  button: {
    margin: theme.spacing(1),
    color: theme.palette.primary.main
  },
  cancelButton: {
    margin: theme.spacing(1),
  },
  buttonText: {
    color: 'white',
  },
  input: {
    display: "none"
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120
  },
  selectEmpty: {
    marginTop: theme.spacing(1)
  },
});

const infuraProvider = new Web3.providers.HttpProvider(
  "https://ropsten.infura.io"
);

// TODO: clean this up
var ENS = require("ethereum-ens");

var ens = new ENS(infuraProvider);

const toBN = amt => {
  return new BigNumber(amt);
};

// Stolen from https://www.oreilly.com/library/view/regular-expressions-cookbook/9781449327453/ch08s15.html
const DOMAIN_REGEX = /^([a-z0-9]+(-[a-z0-9]+)*\.)+[a-z]{2,}$/;

// TODO: refactor into createPayment() and createTokenPayment().
function toWeiBN(amount, denom) {
  let sendAmount = toBN(
    // Assumes amount is valid.
    window.web3.utils.toWei(toBN(amount).toFixed(), denom)
  );

  return sendAmount;
}
// TODO: refactor into createPayment() and createTokenPayment().
function toTokenBN(amount, denom) {
  let tokenInfo = App.ropstenTokenMap[denom];
  console.log("Amount is: " + amount);
  // Assumes amount is valid.
  return toBN(amount).times(toBN("10").pow(tokenInfo.decimals));
}

class NewPayment extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount = () => {
    this.updateBalance(true);
  };

  state = {
    code: niceware.generatePassphrase(6),
    submitOpen: false,
    denom: "wei",
    copied: false,
    showPassword: false,
    sendEther: true,
    //token: 'DAI',
    token: "BOKKY",
    displayBalance: "0",
    realBalance: 0,
    balanceLoaded: false,
    balanceEnough: false,
    resolvedAddr: undefined,
    resolvedNames: {}
  };

  handleClickShowPassword = () => {
    this.setState(state => ({ showPassword: !state.showPassword }));
  };

  refreshCode = event => {
    this.setState({ code: niceware.generatePassphrase(6), copied: false });
  };

  toggleSubmitDialog = bool => event => {
    this.setState({ submitOpen: bool });
  };

  handleSelectChange = event => {
    this.setState({ [event.target.name]: event.target.value });
    // Update balance if we switch to a new token/ denom
    if (event.target.name === "token") {
      this.updateBalance(false);
    } else if (event.target.name === "denom") {
      this.updateBalance(true);
    }
  };

  // Taken from https://docs.ens.domains/dapp-developer-guide/resolving-names
  resolveENSNameToAddr = async name => {
    try {
      let val = this.state.resolvedNames[name];
      if (val === undefined) {
        let addr = await ens.resolver(name).addr();
        let copy = this.state.resolvedNames;
        copy[name] = addr;
        this.setState({ resolvedNames: copy, resolvedAddr: addr });
        console.log("got addr: " + addr);
      } else {
        if (this.state.resolvedAddr !== val) {
          this.setState({ resolvedAddr: val });
        }
      }
    } catch (error) {
      //console.log(error);
      if (undefined !== this.state.resolvedAddr) {
        this.setState({ resolvedAddr: undefined });
      }
    }
  };

  isValidDomainName = name => {
    return DOMAIN_REGEX.test(name);
  };

  //  https://web3js.readthedocs.io/en/1.0/web3-utils.html#towei
  static denomToPrecision = {
    wei: 1,
    Kwei: 3,
    babbage: 3,
    Mwei: 6,
    lovelace: 6,
    Gwei: 9,
    shannon: 9,
    microether: 12,
    szabo: 12,
    milliether: 15,
    finney: 15,
    ether: 18
  };

  static whitelistedTokens = ["BOKKY"];
  /*
        [
        'DAI',
        'BAT',
        'OMG',
        'WTC',
        'BNB',
        'MKR',
        'ZRX',
        'ENJ',
        'LINK',
         'PPT',
    ];
    */

  static selectedDenoms = [
    "wei",
    "Kwei",
    "Mwei",
    "Gwei",
    "microether",
    "milliether",
    "ether"
  ];

  isBadTimeInput = input => {
    return (
      this.props.newPayment.holdTime[input].toString() !==
      parseInt(this.props.newPayment.holdTime[input]).toString()
    );
  };

  handleSwitch = async event => {
    this.updateBalance(!this.state.sendEther);
    this.setState({ sendEther: !this.state.sendEther });
  };

  isBalanceEnough = (balance, amount) => {
    if (!this.isValidAmount(amount)) {
      return false;
    } else {
      let denom = this.state.sendEther ? this.state.denom : this.state.token;
      let rawAmount = this.state.sendEther
        ? toWeiBN(amount, denom)
        : toTokenBN(amount, denom);
      return !toBN(balance)
        .minus(rawAmount)
        .isNegative();
    }
  };

  updateIsBalanceEnough = (balance, amount) => {
    let isBalanceEnough = this.isBalanceEnough(balance, amount);
    this.setState({ balanceEnough: isBalanceEnough });
  };

  async updateBalance(useEth) {
    this.setState({ balanceLoaded: false });
    let balance;

    // TODO: use a function to display the realBalance instead of
    // using two vars.
    let balanceEnough;
    let realBalance;

    if (useEth) {
      realBalance = await this.props.updateTokenBalance("ETH");
      balanceEnough = this.isBalanceEnough(
        realBalance,
        this.props.newPayment.amount
      );
      balance = toBN(realBalance)
        .div(toBN(10).pow(toBN(18)))
        .toFixed();
    } else {
      realBalance = await this.props.updateTokenBalance(this.state.token);
      balanceEnough = this.isBalanceEnough(
        realBalance,
        this.props.newPayment.amount
      );
      balance = toBN(realBalance)
        .div(toBN(10).pow(toBN(App.ropstenTokenMap[this.state.token].decimals)))
        .toFixed();
    }
    console.log("balance: " + balance);
    this.setState({
      displayBalance: balance,
      realBalance: realBalance,
      balanceLoaded: true,
      balanceEnough: balanceEnough
    });
  }

  countDecimals = numStr => {
    if (
      Math.floor(parseFloat(numStr).valueOf()) === parseFloat(numStr).valueOf()
    )
      return 0;
    return numStr.split(".")[1].length || 0;
  };

  isValidAmount = amount => {
    if (amount === undefined || amount === "") {
      return false;
    }
    try {
      if (this.state.sendEther) {
        if (this.state.denom === "wei") {
          if (amount.includes(".")) {
            return false;
          }
        } else if (
          this.countDecimals(amount) >
          parseInt(NewPayment.denomToPrecision[this.state.denom])
        ) {
          return false;
        }

        return true;
      } else {
        // Handle token precision (non-ether)
        let numDecimals = parseInt(
          App.ropstenTokenMap[this.state.token].decimals
        );
        return this.countDecimals(amount) <= numDecimals;
      }
    } catch (error) {
      return false;
    }
  };

  updateAmount = event => {
    this.props.updateNewPayment("amount")(event);
    this.updateIsBalanceEnough(this.state.realBalance, event.target.value);
  };

  render() {
    const { classes, breakpoints, currentBreakpoint } = this.props;
    let passphrase = this.state.code;

    const badAddr =
      this.state.resolvedAddr === undefined &&
      (this.props.newPayment.recipient === undefined ||
        this.props.newPayment.recipient === "" ||
        !window.web3.utils.isAddress(this.props.newPayment.recipient));

    const validAmount = this.isValidAmount(this.props.newPayment.amount);

    const badTimeInput = (() => {
      for (let key in this.props.newPayment.holdTime) {
        if (this.isBadTimeInput(key)) {
          return true;
        }
      }
      return false;
    })();

    const disableSubmit = badAddr || !validAmount || badTimeInput;
    const handleFocus = event => event.target.select();
    const formLabel = {
      fontSize: "1.5em",
      textDecoration: "underline",
      marginBottom: "2px",
      textAlign: "left"
    };

    // TODO: find a better check than isValidDomainName, it's too general
    if (this.isValidDomainName(this.props.newPayment.recipient)) {
      this.resolveENSNameToAddr(this.props.newPayment.recipient);
    }

    return (
      <div style={{
        width: (breakpoints[currentBreakpoint] < breakpoints.desktop) ? '90%' : '70%',
        margin: "auto"
      }}>
          <div style={{ width: "100%", margin: "auto" }}>
            <h3
              style={{
                textAlign: "center",
                fontSize: "1.5em",
                fontWeight: "500",
                marginBottom: "0px"
              }}
            >
              New Payment
            </h3>
            <Divider style={{ width: '30%', margin: 'auto' }} />
          </div>
          <div>
            <p style={formLabel}>Recipient:</p>
            <TextField
              id="outlined-multiline-static-recipient"
              className={classes.textField}
              onChange={this.props.updateNewPayment("recipient")}
              style={{ width: "80%", marginLeft: "32px" }}
              value={this.props.newPayment.recipient}
              disabled={false}
              margin="normal"
              label={"Ethereum address"}
              helperText={badAddr ? "Address is not valid" : ""}
              error={badAddr}
            />
            {this.state.resolvedAddr === undefined ? (
              ""
            ) : (
              <Tooltip title={this.state.resolvedAddr}>
                <CheckCircleIcon style={{ color: "green" }} />
              </Tooltip>
            )}
          </div>
          <div>
            <p style={formLabel}>Amount to send:</p>
            <form className={classes.root} autoComplete="off">
              <FormControlLabel
                control={
                  <Switch
                    checked={!this.state.sendEther}
                    onChange={this.handleSwitch}
                    value="Send Tokens"
                  />
                }
                label="Send ERC20"
              />
            </form>
            <Grid container direction="row">
              <div style={{ width: "60%", marginRight: '10px' }}>
                <TextField
                  id="outlined-multiline-static-amount"
                  className={classes.textField}
                  disabled={false}
                  style={{ width: "80%", marginLeft: '30px' }}
                  value={this.props.newPayment.amount}
                  onChange={this.updateAmount}
                  margin="normal"
                  label={
                    this.state.sendEther ? this.state.denom : this.state.token
                  }
                  type={"number"}
                  helperText={
                    !validAmount
                      ? "Amount is not valid"
                      : !this.state.balanceLoaded
                      ? "Wait for balance to load"
                      : !this.state.balanceEnough
                      ? "Balance is not enough."
                      : ""
                  }
                  error={
                    !validAmount ||
                    !this.state.balanceLoaded ||
                    !this.state.balanceEnough
                  }
                />
              </div>
              <div style={{ width: "30%", marginTop: "2px" }}>
                <FormControl className={classes.formControl}>
                  <InputLabel htmlFor="denom-helper">Denomination</InputLabel>
                  <Select
                    value={
                      this.state.sendEther
                        ? this.state.denom
                        : this.state.token
                    }
                    onChange={this.handleSelectChange}
                    style={{ width: "90%" }}
                    input={
                      <Input
                        name={this.state.sendEther ? "denom" : "token"}
                        id={
                          this.state.sendEther
                            ? "denom-helper"
                            : "token-helper"
                        }
                      />
                    }
                  >
                    {this.state.sendEther
                      ? NewPayment.selectedDenoms.map(key => {
                          return (
                            <MenuItem
                              selected={key === this.state.denom}
                              key={key}
                              value={key}
                            >
                              {key}
                            </MenuItem>
                          );
                        })
                      : NewPayment.whitelistedTokens.map(key => {
                          return (
                            <MenuItem
                              selected={key === this.state.token}
                              key={key}
                              value={key}
                            >
                              {key}
                            </MenuItem>
                          );
                        })}
                  </Select>
                  <FormHelperText />
                </FormControl>
              </div>
            </Grid>
            <div
              style={{ width: "60%", marginLeft: "32px", fontSize: "0.75em" }}
            >
              Your {this.state.sendEther ? "ETH" : this.state.token} balance
              is {this.state.displayBalance}.
            </div>

            <br />
          </div>
          <p style={formLabel}>Available time window:</p>
          <p style={{ fontSize: "0.75em" }}>
            The recipient can only retrieve the funds within this amount of
            time after the transaction is submitted.
          </p>
          <div style={{ marginLeft: "32px" }}>
            {["days", "hours", "minutes"].map(input => {
              const badTime = this.isBadTimeInput(input);
              // See https://stackoverflow.com/questions/45939909/put-length-contraint-in-a-textfield-in-react-js
              return (
                <Fragment key={input}>
                  <TextField
                    id={"outlined-multiline-static-" + input}
                    value={this.props.newPayment.holdTime[input]}
                    className={classes.textField}
                    disabled={false}
                    style={{ width: "20%" }}
                    variant="standard"
                    margin="normal"
                    label={input}
                    onChange={this.props.updateNewPaymentTime(
                      "holdTime",
                      input
                    )}
                    type={"number"}
                    error={badTime}
                    onFocus={handleFocus}
                    helperText={badTime ? "Integers only" : ""}
                    onInput={e => {
                      e.target.value = Math.max(0, parseInt(e.target.value))
                        .toString()
                        .slice(0, 2);
                    }}
                  />
                </Fragment>
              );
            })}
          </div>
          <br />

            <p style={formLabel}>Passphrase: </p>
            <Grid container direction="row">
            <TextField
              id="outlined-multiline-static-passphrase"
              value={passphrase.join(" ")}
              className={classes.textField}
              disabled={true}
              margin="normal"
              label={"Passphrase"}
              style={{ marginLeft: "32px", width: "50%" }}
              type={this.state.showPassword ? "text" : "password"}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="Toggle passphrase visibility"
                      onClick={this.handleClickShowPassword}
                    >
                      {this.state.showPassword ? (
                        <VisibilityOff />
                      ) : (
                        <Visibility />
                      )}

                    </IconButton>
                  </InputAdornment>
                )
              }}
            />
            <Tooltip title={"Generate a new code"}>
              <IconButton
                size="small"
                className={classes.button}
                variant="contained"
                color="secondary"
                onClick={this.refreshCode}
              >
<RefreshIcon />
              </IconButton>
            </Tooltip>
            <CopyToClipboard
              text={passphrase.join(" ")}
              onCopy={() => this.setState({ copied: true })}
            >
              <Tooltip title={"Copy passphrase to clipboard"}>
                <IconButton>
                  <FileCopyIcon />
                </IconButton>
              </Tooltip>
            </CopyToClipboard>{" "}
            {this.state.copied ? (
              <span style={{ color: "red" }}>Copied.</span>
            ) : null}
          </Grid>
          <br />
          <div style={{ width: "90%" }}>
            <Divider />
          </div>
          <Grid
            container
            direction="row"
            justify="space-between"
            style={{ width: "90%" }}
          >
            <div>
              <p style={{ fontSize: "0.7em" }}>
                **All transactions are charged a 0.001 ETH base fee along with a payment fee (separate from
                gas costs) to help maintain this service.
              </p>
            </div>
            <MediaQuery query="(min-width: 1300px)">
              {matches => {
                if (matches) {
                  return (
                    <div>
                      <Button
                        className={classes.button}
                        classes={{
                          label: classes.buttonText,
                        }}
                        variant="contained"
                        onClick={this.toggleSubmitDialog(true)}
                        color="primary"
                        disabled={disableSubmit}
                        size="small"
                      >
                        Submit
                      </Button>

                      <Button
                        className={classes.cancelButton}
                        color="secondary"
                        onClick={this.props.toggleNew(false)}
                        size="small"
                      >
                        Cancel
                      </Button>
                    </div>
                  );
                } else {
                  return (
                    <div style={{ width: '100%' }}>
                      <Grid container direction="row-reverse" justify="center">  
                        <Button
                          className={classes.button}
                          onClick={this.toggleSubmitDialog(true)}
                          disabled={disableSubmit}
                          size="medium"
                        >
                          Submit
                        </Button>
                        <Button
                          className={classes.button}
                          onClick={this.props.toggleNew(false)}
                          size="medium"
                        >
                          Cancel
                        </Button>
                      </Grid>
                    </div>
                  )
                }
              }}
            </MediaQuery>
              {disableSubmit ? "" :
                  <SubmitDialog
                      open={this.state.submitOpen}
                      newPayment={this.props.newPayment}
                      badAddr={badAddr}
                      code={passphrase.join(" ")}
                      toggleSubmit={this.toggleSubmitDialog}
                      createPayment={this.props.createPayment}
                      denom={this.state.denom}
                      token={this.state.token}
                      sendEther={this.state.sendEther}
                      etherclearContractInstance={
                          this.props.etherclearContractInstance
                      }
                  />
              }
          </Grid>
          <br />
            {disableSubmit ? (
              <div>
                <p style={{ color: "red", fontSize: '0.75em', textAlign: 'center', marginBottom: '32px' }}>
                  Correct errors before submitting.
                </p>
              </div>
            ) : (
              ""
            )}
        </div>
    );
  }
}

NewPayment.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles, { withTheme: theme })(withBreakpoints(NewPayment));
