import React, { Component } from "react";
import {
  Divider,
  Grid,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
  Tooltip
} from "@material-ui/core";
import MediaQuery from "react-responsive";
import { createMuiTheme} from '@material-ui/core/styles';
import {withTheme } from '@material-ui/styles'
import RefreshIcon from "@material-ui/icons/Refresh";
import PaymentRow from "./PaymentRow";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

// custom mui theme
const theme = createMuiTheme();

class PaymentsIndex extends Component {
  constructor(props) {
    super(props);
    // payment type will always be OPEN or CLOSED
    this.state = {
      paymentType: "all",
      openPayments: [],
      closedPayments: [],
      loading: true
    };
    this.latestTsFirst = this.latestTsFirst.bind(this);
    this.filterPayments = this.filterPayments.bind(this);
    this.onFilterChange = this.onFilterChange.bind(this);
  }

  // when component mounts will filter payments and display
  async componentDidMount() {
    await this.filterPayments();
    // when all setup make loading false
    // TODO: necessary??
    await this.setState({ loading: false });
  }

  // Custom comparator to sort payments with the most
  // recent at the top.
  latestTsFirst = (a, b) => {
    if (a.paymentOpenTime > b.paymentOpenTime) return -1;
    if (b.paymentOpenTime > a.paymentOpenTime) return 1;

    return 0;
  };

  refreshPayments = async () => {
    await this.props.getPaymentEvents();
    await this.filterPayments();
  };

  filterPayments = async () => {
    let sortedPayments = this.props.allPayments.sort(this.latestTsFirst);
    let openPayments = [];
    let closedPayments = [];

    // TODO: should I be making a copy here?
    for (let i = 0; i < sortedPayments.length; i++) {
      if (sortedPayments[i].state === 0) {
        openPayments.push(sortedPayments[i]);
      } else {
        closedPayments.push(sortedPayments[i]);
      }
    }

    this.setState({
      openPayments,
      closedPayments
    });
  };

  /**
   * OnFilterChange takes in the checked checkbox from
   * clicked item and sets state of paymentType to value
   *
   * @param
   * checked: obj
   *
   */
  onFilterChange = item => this.setState({ paymentType: item.value });

  render() {
    const { allPayments } = this.props;
    const { paymentType, openPayments, closedPayments, loading } = this.state;

    if (loading) {
      return <h1>Loading...</h1>;
    }

    // when filtering by age validDaysInput makes sure it is
    // an ok input
    const validDaysInput = !isNaN(parseInt(this.props.daysInput));

    // makes header mobile responsive
    const headerWidth = window.screen.width < 1200 ? "75%" : "22%";

    const headerSize = window.screen.width < 1200 ? "1.7em" : "2em";

    const dynamicFont = window.screen.width < 1200 ? ".8em" : "1.5em";

    return (
      <div style={{ padding: "40px" }}>
        <div style={{ width: headerWidth, margin: "auto" }}>
          <h3
            style={{
              fontSize: headerSize,
              textAlign: "center",
              fontWeight: "500",
              marginBottom: "0px"
            }}
          >
            My Payments
          </h3>
          <Divider />
        </div>
        <div style={{ marginLeft: '24px' }}>
          <Grid container direction="row">
            <p style={{ fontWeight: "500", fontSize: "1.2em" }}>
              Filter Payments
            </p>
            <IconButton
              onClick={this.refreshPayments}
              disabled={
                this.props.etherclearContractInstance === undefined ||
                !validDaysInput
              }
            >
              <Tooltip title={"Refresh payment list"}>
                <RefreshIcon />
              </Tooltip>
            </IconButton>
          </Grid>

          <TextField
            id="outlined-multiline-static"
            onChange={this.props.updateDaysInput}
            value={this.props.daysInput}
            disabled={this.props.etherclearContractInstance === undefined}
            margin="normal"
            label={"Past days"}
            helperText={
              validDaysInput
                ? "# of past days to display payments"
                : "Invalid input."
            }
            error={!validDaysInput}
          />

          <Grid container direction="column">
            <FormControlLabel
              control={
                <Checkbox
                  checked={paymentType === "all"}
                  onChange={event => this.onFilterChange(event.target)}
                  value="all"
                />
              }
              style={{ fontSize: "0.75em" }}
              label="All Payments"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={paymentType === "OPEN"}
                  onChange={event => this.onFilterChange(event.target)}
                  value="OPEN"
                />
              }
              label="Open Payments"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={paymentType === "CLOSED"}
                  onChange={event => this.onFilterChange(event.target)}
                  value="CLOSED"
                />
              }
              label="Closed Payments"
            />
          </Grid>
        </div>

        <div style={{ width: "100%", paddingLeft: '16px' }}>
          {paymentType === "OPEN" ? (
            <div>
              <h3
                style={{
                  fontSize: "1.5em",
                  marginBottom: "4px",
                  fontWeight: "400"
                }}
              >
                Open Payments
              </h3>

              <Table style={{ minWidth: "330px" }}>
                <TableHead>
                  <TableRow>
                    <TableCell>Date Sent</TableCell>
                    <MediaQuery query="(min-device-width: 1200px)">
                      {matched => {
                        if (matched) {
                          return <TableCell>Amount Sent</TableCell>;
                        } else {
                          return "";
                        }
                      }}
                    </MediaQuery>
                    <TableCell>Token</TableCell>
                    <TableCell>Payment Status</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {openPayments.length > 0
                    ? openPayments.map(payment => (
                        <PaymentRow key={payment.paymentId} payment={payment} />
                      ))
                    : <tr/>}
                </TableBody>
              </Table>
              {openPayments.length > 0 ? (
                ""
              ) : (
                <div style={{ marginTop: "32px" }}>
                  <h3
                    style={{
                      color: "gray",
                      textAlign: "center",
                      textDecoration: "underline",
                      fontSize: dynamicFont,
                      fontWeight: "normal"
                    }}
                  >
                    No open payments for this date range.
                  </h3>
                </div>
              )}
            </div>
          ) : paymentType === "CLOSED" ? (
            <div>
              <h3
                style={{
                  fontSize: "1.5em",
                  marginBottom: "4px",
                  fontWeight: "400"
                }}
              >
                Closed Payments
              </h3>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Date Sent</TableCell>
                    <MediaQuery query="(min-device-width: 1200px)">
                      {matched => {
                        if (matched) {
                          return <TableCell>Amount Sent</TableCell>;
                        } else {
                          return "";
                        }
                      }}
                    </MediaQuery>
                    <TableCell>Token</TableCell>
                    <TableCell>Payment Status</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {closedPayments.length > 0
                    ? closedPayments.map(payment => (
                        <PaymentRow key={payment.paymentId} payment={payment} />
                      ))
                    : <tr/>}
                </TableBody>
              </Table>
              {closedPayments.length > 0 ? (
                ""
              ) : (
                <div style={{ marginTop: "32px" }}>
                  <h3
                    style={{
                      color: "gray",
                      textAlign: "center",
                      textDecoration: "underline",
                      fontSize: dynamicFont,
                      fontWeight: "normal"
                    }}
                  >
                    No closed payments for this date range.
                  </h3>
                </div>
              )}
            </div>
          ) : (
            <div>
              <h3
                style={{
                  fontSize: "1.5em",
                  marginBottom: "4px",
                  fontWeight: "400"
                }}
              >
                All Payments
              </h3>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Date Sent</TableCell>
                    <MediaQuery query="(min-device-width: 1200px)">
                      {matched => {
                        if (matched) {
                          return <TableCell>Amount Sent</TableCell>;
                        } else {
                          return "";
                        }
                      }}
                    </MediaQuery>
                    <TableCell>Token</TableCell>
                    <TableCell>Payment Status</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody style={{ width: "90%" }}>
                  {allPayments.length > 0
                    ? allPayments.map(payment => (
                        <PaymentRow key={payment.paymentId} payment={payment} />
                      ))
                    : <tr/>}
                </TableBody>
              </Table>
              {allPayments.length > 0 ? (
                ""
              ) : (
                <div style={{ marginTop: "32px" }}>
                  <h3
                    style={{
                      color: "gray",
                      textAlign: "center",
                      textDecoration: "underline",
                      fontSize: dynamicFont,
                      fontWeight: "normal"
                    }}
                  >
                    No payments for this date range.
                  </h3>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default withTheme(PaymentsIndex);
