  // TODO: does this need events as well?
  updateTokenBalance = async name => {
    let tokenInfo = App.ropstenTokenMap[name];

    // TODO: this should probably be accounted for beforehand.
    if (this.state.accounts[0] === undefined) {
      return 0;
    }

    if (name === "ETH") {
      return window.web3.eth.getBalance(this.state.accounts[0]);
    }

    let tokenContractInstance;
    let erc20 = contract(IERC20ContractDefinition);
    // TODO: This depends on whether the provider is metamask.
    if (window.ethereum !== undefined) {
      erc20.setProvider(window.ethereum);
    } else if (window.web3.currentProvider !== undefined) {
      erc20.setProvider(window.web3.currentProvider);
    } else {
      console.log(
        "Could not obtain provider to initialize ERC-20 contract instance"
      );
    }

    return await erc20
      .at(tokenInfo.addr)
      .then(result => {
        console.log("Got token contract result");
        tokenContractInstance = result;
        // Get balance
        return result.balanceOf(this.state.accounts[0], {
          from: this.state.accounts[0],
          gas: GAS
        });
      })
      .then(balance => {
        console.log(balance.toString());
        return balance.toString();
      })
      .catch(error => {
        console.log(error);
      });
  };

  // TODO: still need to check balances and make sure the user has enough
  // to spend the token.
  // In this case denom is the token symbol.
  createTokenPayment = async (amount, recipient, holdTime, codeHash, denom) => {
    let tokenInfo = App.ropstenTokenMap[denom];
    // Currently only supports Bokky Poo Bah test tokens
    // TODO: Reuse the contract instance instead of fetching it
    // each time.
    let tokenContractInstance;

    let sendAmount = toBN(amount).times(toBN(10).pow(tokenInfo.decimals));
    let baseFee = toBN(window.web3.utils.toWei(App.baseFeeEth, "ether"));

    let paymentFee = toBN(parseInt(sendAmount * App.paymentFeePercent));

    // We need to send the original amount to the contract for it to calculate the
    // fee with, but request approval to transfer the amount with the fee.
    let amountWithFee = sendAmount.plus(paymentFee);

    // TODO: don't need to do this twice (already do it to get the balance).
    let erc20 = contract(IERC20ContractDefinition);
    // TODO: Use of window.ethereum depends on whether the provider is metamask.
    // Maybe just check if window.ethereum is provided?
    if (window.ethereum !== undefined) {
      erc20.setProvider(window.ethereum);
    } else if (window.web3.currentProvider !== undefined) {
      erc20.setProvider(window.web3.currentProvider);
    } else {
      console.log(
        "Could not obtain provider to initialize ERC-20 contract instance"
      );
    }

    // TODO: pass in the newPayment to display info from it
    let event = {
      ts: Date.now(),
      name: "Create " + denom + " payment",
      msgLines: ["Created the raw transaction."],
      loading: true,
      statusText: "Waiting for user to sign first transaction"
    };

    this.updateEvents(event, event.ts);
    /*
    erc20
      .at(tokenInfo.addr)
      .then(result => {
        console.log("Got token contract result");
        tokenContractInstance = result;
        */
    // Make approval
    // TODO: implement batch transactions once the issue
    // with ordering is resolved, otherwise the user has to
    // click through to the approve transaction first and could get the order
    // mixed up.
    // https://github.com/MetaMask/metamask-extension/issues/5852
    /*
              let batch = new window.web3.BatchRequest();
              batch.add(etherclearContractInstance.createTokenPayment(
                  tokenInfo.addr,
                  sendAmount.toString(10),
                  recipient,
                  holdTime,
                  codeHash,
                  {
                      from: this.state.accounts[0],
                      gas: GAS,
                      value: baseFee.toString(10),
                  }));
              batch.add(result.approve(etherclearContractDefinition.networks[ROPSTEN].address, sendAmount.toString(10),
                  {
                      from: this.state.accounts[0],
                      gas: GAS,
                  }));
              return batch.execute();
              */

    let result = await erc20.at(tokenInfo.addr);
    result
      .approve(
        etherclearContractDefinition.networks[ROPSTEN].address,
        amountWithFee.toString(10),
        {
          from: this.state.accounts[0],
          gas: GAS
        }
      )
      .on("transactionHash", hash => {
        this.updateEvent(
          event,
          event.ts,
          this.displayHashWithLinkMsg("Received transaction hash:", hash),
          "Waiting for user to sign second transaction"
        );

          // TODO: Either batch these or refactor to make
          // the calls cleaner.
          etherclearContractInstance
              .createTokenPayment(
                  tokenInfo.addr,
                  sendAmount.toString(10),
                  recipient,
                  holdTime,
                  codeHash,
                  {
                      from: this.state.accounts[0],
                      gas: GAS,
                      value: baseFee.toString(10)
                  }
              )
              .on("transactionHash", hash => {
                  this.updateEvent(
                      event,
                      event.ts,
                      this.displayHashWithLinkMsg("Received transaction hash:", hash),
                      "Waiting for transaction result"
                  );
              })
              .then(result => {
                  this.updateEventForSuccess(
                      event,
                      event.ts,
                      "Token payment created successfully."
                  );
                  this.getPaymentEvents();
              })
              .catch(error => {
                  console.log(error);
                  this.updateEventForError(event, event.ts, error.toString());
              });

      })
      .then(approval => {
        console.log("Got token transfer approval");
          this.updateEventForSuccess(
              event,
              event.ts,
              "Got token transfer approval."
          );
      })
      .catch(error => {
        console.log(error);
        this.updateEventForError(event, event.ts, error.toString());
      });
  };

