  retrieveFundsFeeless = async (txnId, code, sender) => {
    var sigUtil = require("eth-sig-util");
    var ethUtil = require("ethereumjs-util");

    // Taken from example at https://github.com/danfinlay/js-eth-personal-sign-examples
    let recipient = window.web3.utils.toChecksumAddress(this.state.accounts[0]);
    console.log("recipient is: " + recipient);

    let msgParams = JSON.stringify({
      types: {
        EIP712Domain: [
          { name: "name", type: "string" },
          { name: "version", type: "string" },
          { name: "chainId", type: "uint256" },
          { name: "verifyingContract", type: "address" },
          { name: "salt", type: "bytes32" }
        ],
        RetrieveFundsRequest: [
          { name: "txnId", type: "uint256" },
          { name: "sender", type: "address" },
          { name: "recipient", type: "address" },
          { name: "passphrase", type: "string" }
        ]
      },
      primaryType: "RetrieveFundsRequest",
      domain: {
        name: "EtherClear",
        version: "1",
        chainId: 3,
        verifyingContract: "0x1C56346CD2A2Bf3202F771f50d3D14a367B48070",
        salt:
          "0xf2d857f4a3edcb9b78b4d503bfe733db1e3f6cdc2b7971ee739626c97e86a558"
      },
      message: {
        txnId: txnId,
        sender: sender,
        recipient: recipient,
        passphrase: code
      }
    });

    console.log(
      "CLICKED, SENDING PERSONAL SIGN REQ",
      "from",
      recipient,
      msgParams
    );

    var params = [recipient, msgParams];
    console.dir(params);
    var method = "eth_signTypedData_v3";

    let event = {
      ts: Date.now(),
      name: "Retrieve funds (feeless)",
      msgLines: ["Created the raw signed message."],
      loading: true,
      statusText: "Waiting for user to sign"
    };

    this.updateEvents(event, event.ts);

    window.web3.currentProvider.sendAsync(
      {
        method,
        params,
        recipient
      },
      (err, result) => {
        if (err) return console.dir(err);
        if (result.error) {
          this.updateEventForError(event, event.ts, result.error);
        }
        if (result.error) return console.error("ERROR", result);
        console.log("TYPED SIGNED:" + JSON.stringify(result.result));

        const recovered = sigUtil.recoverTypedSignature({
          data: JSON.parse(msgParams),
          sig: result.result
        });

        if (
          ethUtil.toChecksumAddress(recovered) ===
          ethUtil.toChecksumAddress(recipient)
        ) {
          let msgLine =
            "Successfully ecRecovered signer as " +
            recipient +
            ". Submitting to the relayer service.";
          this.updateEvent(event, event.ts, msgLine, "Submitting to relayer");
        } else {
          let errMsg =
            "Failed to verify signer when comparing " +
            result +
            " to " +
            recipient;
          this.updateEventForError(event, event.ts, errMsg);

          this.updateEvents(event, event.ts);
        }

        // TODO: remove once api-config usage is tested.
        /*
        let localEndpoint = "http://localhost:8080/tx";
        let relayerEndpoint =
          "https://prod-dot-relayer-dot-etherclear.appspot.com/tx";
          */

        let relayerEndpoint = API_ROOT + "tx";

        fetch(relayerEndpoint, {
          method: "POST",
          mode: "cors",
          headers: {
            "Content-Type": "application/json; charset=utf-8",
            // TODO: this needs to be more restrictive.
            "Access-Control-Allow-Origin": "*"
          },
          body: JSON.stringify({
            msg: msgParams,
            from: recipient,
            sig: result.result
          })
        })
          .then(response => {
            let json = response.json();
            this.updateEvent(
              event,
              event.ts,
              "Got server response.",
              "Processing response"
            );
            return json;
          })
          .then(async json => {
            console.log("Parsed response JSON: ", JSON.stringify(json));
            if (json.err !== undefined) {
              this.updateEventForError(
                event,
                event.ts,
                JSON.stringify(json.err)
              );
            } else if (json.res !== undefined) {
              let receipt = null;

              this.updateEvent(
                event,
                event.ts,
                this.displayHashWithLinkMsg(
                  "Server submitted transaction. Polling the chain for transaction:",
                  json.res
                ),
                "Waiting for transaction result"
              );

              while (receipt === null) {
                await sleep(5000);
                receipt = await window.web3.eth.getTransactionReceipt(json.res);
                console.log("Current receipt is: " + receipt);
                console.log("Checking for transaction receipt...");
              }
              if (receipt.status === false) {
                console.log("Transaction failed.");
                this.updateEventForError(
                  event,
                  event.ts,
                  "Transaction failed."
                );

                this.updateEvents(event, event.ts);
              } else if (receipt.status === true) {
                console.log("Transaction succeeded!");
                this.updateEventForSuccess(
                  event,
                  event.ts,
                  "Transaction succeeded"
                );

                this.getPaymentEvents();
              }
            } else {
              this.updateEventForError(
                event,
                event.ts,
                "No result was set in the response."
              );
            }
          })
          .catch(ex => {
            console.log("parsing failed", ex);
            this.updateEventForError(
              event,
              event.ts,
              "Parsing response failed: " + ex
            );
          });

        // TODO: remove once signature debugging is complete.
        //let sig = result.result;
        //let msg = JSON.parse(msgParams).message;
        //this.props.checkRetrieveSignature(msg, sig);
      }
    );
  };

