import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom';
import {
  Grid,
  TextField,
  Tooltip,
  IconButton,
  Fab,
} from '@material-ui/core';
import List from '@material-ui/core/List';
import ListIcon from '@material-ui/icons/List';
import AddIcon from '@material-ui/icons/Add';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import { CopyToClipboard } from "react-copy-to-clipboard";
import FileCopyIcon from '@material-ui/icons/FileCopy';
import MailIcon from '@material-ui/icons/Mail';


const MobileSideDrawer = (props) => {
  const {
    ROPSTEN,
    contractDisplayName,
    displayName,
    addr,
    noAddr,
    etherclearContractInstance,
    etherclearContractDefinition,
  } = props;
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (side, open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [side]: open });
  };

  // style for nav links
  const linkStyles = {
    textDecoration: 'none',
  };

  const sideList = side => (
    <div
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >
      <div>
        { /* Mobile Header Goes in here */}

        <div style={{ width: "95%" }}>
          <Grid container direction="row" justify="flex-start"> 
            <Button color="primary" onClick={toggleDrawer('left', false)}><ArrowBackIcon /></Button>
          </Grid>
          <div style={{ width: "90%", margin: "auto" }}>
            <Grid container direction="row" justify="center">
              <img src={require('./logo.svg')} alt="Etherclear" style={{ width: '200px', marginTop: '32px', marginBottom: '24px' }} />
            </Grid>
            <Grid container direction="row">
              <div>
                <div>
                  <p
                    style={{
                      textAlign: "left",
                      marginLeft: "8px",
                      fontSize: "1em",
                      marginBottom: "0px"
                    }}
                  >
                    <a
                      href={
                        "https://ropsten.etherscan.io/address/" +
                        etherclearContractDefinition
                          .networks[ROPSTEN].address
                      }
                      target={"_blank"}
                    >
                      Contract:{" "}
                    </a>
                  </p>
                  <TextField
                    disabled
                    style={{
                      marginLeft: "24px",
                      width: "70%",
                      marginBottom: "16px"
                    }}
                    value={
                      contractDisplayName === undefined
                        ? ""
                        : contractDisplayName
                    }
                  />
                </div>
                <div>
                  <Grid container direction="row">
                    <p
                      style={{
                        textAlign: "left",
                        marginLeft: "8px",
                        fontSize: "1em",
                        marginBottom: "0px"
                      }}
                    >
                      My Address:
                    </p>
                    <TextField
                      disabled
                      style={{
                        marginLeft: "24px",
                        width: "70%",
                        marginBottom: "16px"
                      }}
                      value={
                        displayName === undefined
                          ? ""
                          : displayName
                      }
                    />
                    <CopyToClipboard
                      text={addr}
                      disabled={
                        addr === noAddr ||
                        addr === undefined
                      }
                    >
                      <Tooltip
                        title={"Copy address to clipboard"}
                      >
                        <IconButton>
                          <FileCopyIcon />
                        </IconButton>
                      </Tooltip>
                    </CopyToClipboard>
                  </Grid>
                </div>
              </div>
            </Grid>
          </div>

        </div>
      </div>
      <Divider />
      <List>
        <ListItem button >
          <Link to="/" style={linkStyles}>
            <ListItemText primary="Home" />
          </Link>
        </ListItem>
        <ListItem button >
          <Link to="/" style={linkStyles}>
            <ListItemText primary="Settings" />
          </Link>
        </ListItem>
        <ListItem button >
          <a href="https://etherclearapp.github.io/etherclear_docs/#/" style={linkStyles} target={"_blank"}>
            <ListItemText primary="What is Etherclear?" />
          </a>
        </ListItem>
      </List>

    </div>
  );

  return (
    <div>
      <Fab color="primary" size="small" onClick={toggleDrawer('left', true)}><ListIcon /></Fab>
      <Drawer open={state.left} onClose={toggleDrawer('left', false)}>
        {sideList('left')}
      </Drawer>
    </div>
  );
}

export default MobileSideDrawer;
